import * as React from "react"
import { Link, graphql, useStaticQuery } from "gatsby"
import { StaticImage } from "gatsby-plugin-image"
import BackgroundImage from 'gatsby-background-image'

import Layout from "../components/layout"
import FullScreenSection from "../components/full-screen-section"
import Seo from "../components/seo"

import styled from 'styled-components'

const IndexPage = () => {

  const data = useStaticQuery(
    graphql`
      query {
        bg1:

        file(relativePath: { eq: "bg-3d-1.jpg" }) {
          childImageSharp {
            fluid(quality: 90, maxWidth: 1920) {
              ...GatsbyImageSharpFluid_noBase64
            }
          }
        }

        bg2:
        file(relativePath: { eq: "bg-3d-2.jpg" }) {
          childImageSharp {
            fluid(quality: 90, maxWidth: 1920) {
              ...GatsbyImageSharpFluid_noBase64
            }
          }
        }

      }
    `
  )

  // const data2 = useStaticQuery(
  //   graphql`
  //     query {
  //       desktop: file(relativePath: { eq: "obj.jpg" }) {
  //         childImageSharp {
  //           fluid(quality: 90, maxWidth: 1920) {
  //             ...GatsbyImageSharpFluid_noBase64
  //           }
  //         }
  //       }
  //     }
  //   `
  // )  

  const bg1 = data.bg1.childImageSharp.fluid
  const bg2 = data.bg2.childImageSharp.fluid

  return (
    <Layout>
      <Seo title="Home" />

      <FullScreenSection>

        <BackgroundImage
          Tag="section"
          className={'container-fluid fullheight-ribbon d-flex flex-column'}
          fluid={bg1}
          backgroundColor={`#040e18`}
        >

          <div className={`row d-flex flex-1`}>

            <div className={`col pt-1 d-flex flex-column`}>

              <div className={`col-lg-6 col-xl-6 col`}>

                <h1 className={`text-white text-end bg-black mt-5 pb-0 mb-0`}>Experiencia 3D</h1><p className={`text-black text-end pt-0 mt-0`}>Entorno 3d donde el usuario puede visualizar la obra de Bonati, Vial y Ortúzar, con el color y la textura del proyecto original.</p>
                

              </div>

              <div className={`col d-flex flex-1 flex-column flex-items-middle space-between`}>

                <div className={`col-3`}><p className={`text-right mb-0 pb-0`}><span className={`text-white text-end bg-black pb-0 mb-0 me-1 fs-4`}>Modelo 3D</span></p><span className={`text-white fs-6`}>Desplazate por el mural del paso inferior Santa Lucía desde una escala peatonal.</span></div>
                <div className={`col-3 offset-lg-9 offset-xl-9`}><p className={`text-right mb-0 pb-0`}><span className={`text-white text-end bg-black pb-0 mb-0 me-2 fs-4`}>Recorridos 360</span></p><span className={`text-white fs-6`}>Visualizar los videos realizados con el modelo 3D haciendo recorridos en velocidad por Diagonal Paraguay y la calle Carmen.</span></div>
                <div className={`col-3 offset-lg-4 offset-xl-4 pb-5`}><p className={`text-right mb-0 pb-0`}><span className={`text-white text-end bg-black pb-0 mb-0 ms-5 fs-4 mb-5`}>Vistas 360</span></p><span className={`text-white fs-6`}>Desde un punto fijo, sin velocidad observar el entorno desde ciertos ángulos.</span></div>

              </div>
            </div>                        

          </div>

          <div className={`row ribbon no-gutters`}>

            <div className={`col-3 ribbon-blue`}></div>
            <div className={`col-2 ribbon-red`}></div>
            <div className={`col-4 ribbon-navy`}></div>
            <div className={`col-3 ribbon-gray`}></div>

          </div>

        </BackgroundImage>

      </FullScreenSection>

      <FullScreenSection>

        <BackgroundImage
          Tag="section"
          className={'container-fluid fullheight-ribbon d-flex flex-column'}
          fluid={bg2}
          backgroundColor={`#040e18`}
        >

          <div className={`row d-flex flex-1`}>



              <div className={`col d-flex`}>

                <div className={`col d-flex flex-1 flex-column items-end pe-5`}>
                  <p className={`text-white text-center ps-1 fs-4 mb-0 pb-0`}>Modelo 3D</p>
                </div>

                <div className={`col d-flex flex-1 flex-column items-end pe-5`}>
                  <p className={`text-white text-center ps-1 fs-4 mb-0 pb-0`}>Vistas 360</p>
                </div>

                <div className={`col d-flex flex-1 flex-column items-end pe-5`}>
                  <p className={`text-white text-center ps-1 fs-4 mb-0 pb-0`}>Recorridos 3D</p>
                </div>

              </div>


                                    

          </div>

          <div className={`row d-flex flex-1`}>
              <div className={`col d-flex flex-1 items-end`}>
                <div className={`d-flex flex-1 items-end`}>
                  <i className={`text-white fab fa-youtube display-5`}></i>
                  <i className={`text-white fas fa-cube display-5`}></i>
                </div>
              </div>            
          </div>
            
            <div className={`row ribbon no-gutters`}>

              <div className={`col-3 ribbon-blue`}></div>
              <div className={`col-2 ribbon-red`}></div>
              <div className={`col-4 ribbon-navy`}></div>
              <div className={`col-3 ribbon-gray`}></div>

            </div>

        </BackgroundImage>

      </FullScreenSection>

    </Layout>
  )

}

const StyledBackgroundSection = styled(IndexPage)`
  width: 100%;
  background-position: bottom center;
  background-repeat: repeat-y;
  background-size: cover;
`

export default IndexPage
